<template>
  <div class="topic2">
    <vue-esign
      id="canvas"
      ref="esign"
      :width="1000"
      :height="500"
      class="esign-box"
      :isCrop="isCrop"
      :lineWidth="lineWidth"
      :lineColor="lineColor"
      :bgColor.sync="bgColor"
    />
    <div style="text-align:center;margin-top:2vh">
      <el-button @click="handleReset" size="mini">清空</el-button>
      <el-button type="primary" @click="handleGenerate" size="mini"
        >保存</el-button
      >
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      lineWidth: 6,
      lineColor: "red",
      bgColor: "#ffffff",
      resultImg: "",
      isCrop: false
    };
  },
  methods: {
    handleGenerate() {
      // 获取base64
      var _this = this;
      _this.$refs.esign
        .generate()
        .then(res => {
          // 转成文件
          var blob = _this.dataURLtoBlob(res);
          var tofile = _this.blobToFile(blob, "签名.jpg");
          setTimeout(async () => {
            // const formData = new FormData();
            // formData.append("file", tofile, tofile.name);
            // formData.append("fileType", 9);
            // console.log(tofile);

            const link = document.createElement("a");
            link.style.display = "none";
            link.href = URL.createObjectURL(tofile);
            link.setAttribute("download", "签名.jpg");
            document.body.appendChild(link);
            link.click();
          });
        })
        // eslint-disable-next-line no-unused-vars
        .catch(err => {
          // 画布没有签字时提示
          this.$notify({
            title: "提示",
            message: "失败",
            type: "warning"
          });
        });
    },
    // 将base64转换为blob
    dataURLtoBlob(dataurl) {
      var arr = dataurl.split(",");
      var mime = arr[0].match(/:(.*?);/)[1];
      var bstr = atob(arr[1]);
      var n = bstr.length;
      var u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new Blob([u8arr], { type: mime });
    },
    // 将blob转换为file
    blobToFile(theBlob, fileName) {
      theBlob.lastModifiedDate = new Date();
      theBlob.name = fileName;
      return theBlob;
    },
    handleReset() {
      // 清除
      if (this.$refs.esign) {
        this.$refs.esign.reset();
      }
      var obj = document.getElementById("canvas");
      obj.style.backgroundColor = "#ffffff";
      this.bgColor = "#ffffff";
    }
  }
};
</script>

<style lang="less" scoped>
.topic2 {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .esign-box {
    width: 100%;
    height: 100%;
    border: 1px solid black;
  }
}
</style>
